import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/sc',
      name: 'sc',
      component: resolve => require(['@/pages/sc.vue'], resolve),
    },
    {
      path: '/rmjq',
      name: 'rmjq',
      component: resolve => require(['@/pages/rmjq.vue'], resolve),
    },
    {
      path: '/personalInfo',
      name: 'personalInfo',
      component: resolve => require(['@/pages/personalInfo.vue'], resolve),
    },
  ]
})



