  import {appId} from './api/config'
  import request from '@/util/request.util.js'
  import {userInfo} from './api/userInfo.js'
  export default {
    name: 'app',
    data(){
     return{
       tabIndex:0
     }
    },
    mounted(){
      this._getUserInfo()
    },
    methods:{
      changeIndex(){
          console.log(99999)
      },
      _getUserInfo(){
//        window.localStorage.setItem('currenturl', window.location.href);
        if (!window.localStorage.getItem('openid')) {
          let code = request.getQueryParameter('code');
//          let currenturl=window.location.href.split('#')[0];
          let currenturl = window.location.href
          if (!code) {
//            window.localStorage.setItem('currenturl',window.location.href.split('#')[0]);
            window.localStorage.setItem('currenturl', window.location.href);
//            window.localStorage.setItem('tableid',window.location.href.split('=')[1]);
            location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" + appId + "&redirect_uri=" + encodeURIComponent(currenturl) + "&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect";
          } else {
            userInfo({code: code}).then(res => {
              console.log(res)
              if (res.success) {
                  console.log(9999)
                let openid = res.openid
                window.localStorage.setItem('openid', openid)
                window.location.href = window.localStorage.getItem('currenturl');
//                user()
//                this._getList()
              } else {
//                this.MessageBox('提示', res.data.Message)
              }
            }, res => {
//              this.MessageBox('提示', '网络连接失败！')
            })
          }
        } else {
//          user()
//          this._getList()
        }
      },
    }
  }




