function getQueryParameters() {
    var url = location.search;
    var map = {};
    //��ȡurl��"?"������ִ�
    if (url.indexOf("?") != -1) {
        var str = url.substr(1);
        var strs = str.split("&");
        for (var i = 0; i < strs.length; i++) {
            map[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
        }
    }
    return map;
}

